body {
    background-color: #192824;
}


#FullPageBoss{
    height: 700px;
    width: 100%;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: row;
    align-items: center;
   
    margin-top: 20px;
}


#LeftSide{
    /* border: 1px solid wheat; */
    height: 100%;
    width: 32%;
    display: flex;
    flex-direction: column;
}


#LeftsECTION1{
    height: 300px;
    width: 100%;
    /* border: 1px solid red; */
    padding-left: 10px;
}


#LeftsECTION1 h2{
    font-family: Calibri;
    color: white;
    font-size: 80px;
    margin: 7px 15px;
}


#LeftsECTION1 h3{
    font-family: Calibri;
    font-size: 55px;
    color: rgb(0, 238, 255);
    margin: -19px 17px;
}

#LeftsECTION1 span{
    color: rgb(0, 238, 255);
    font-size: 15px;
    font-family: Calibri;
    margin: -10px 252px;
    position: absolute;
    opacity: 0.5;
}

#LeftsECTION1 hr{
  width: 295px;
  margin: 20px 111px;
  background-color: rgb(0, 238, 255);
  border: none;
  height: 1px;
  opacity: 0.5;
}


#Center{
    /* border: 1px solid wheat; */
    height: 100%;
    width: 34.33%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#MentorsAndStudents{
    height: 60px;
    width: 500px;
    /* border: 1px solid red; */
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-size: Calibri;
}

#mentors{
    background-color: #00000042;
    height: 50px;
    width: 90px;
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 6px;
}

.mentor1{
    background-color: #a2ccd32a;
    height: 30px;
    width: 30px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Calibri;
    color: rgb(0, 238, 255);
    opacity: 0.7 ;
}

.mentor1:hover{
    color: rgb(0, 238, 255);
    opacity: 1;
    cursor: pointer;
    font-size: 20px;    
}

#students{
    background-color: #00000042;
    height: 50px;
    width: 420px;
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 6px;
}


.studentClass{
    background-color: #a2ccd32a;
    height: 30px;
    width: 30px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Calibri;
    color: rgb(0, 238, 255);
    opacity: 0.7 ;
}



.studentClass:hover{
    color: rgb(0, 238, 255);
    opacity: 1;
    cursor: pointer;
    font-size: 20px;
}

#RightSide{
    /* border: 1px solid wheat; */
    height: 100%;
    width: 30%;
    margin-left: 15px;
    background: rgba(114, 114, 114, 0.13);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(9px);
    -webkit-backdrop-filter: blur(9px);
    border-radius: 8px;
}

#ProjectReal{
    height: 100%;
    width: 100%;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

#headerright{
    height: 40px;
    min-width: 250px;
    width: auto;
    background-color: #00000042;
    border-radius: 8px;
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}


#headerright h3{
color: white;
font-family: Calibri;
}


#RightPTag{
    min-height: 280px;
    height: auto;
    background-color: #a2ccd32a;
    width: 400px;
    margin-top: 30px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 15px;
}

#ProjectReal p{ 
    font-size: 17px;
    font-family: Calibri;
    font-weight: bolder;
    opacity: 0.6;
    color: white;
}

#imgRightSide{
    background-color: #00000042;
    min-height: 200px;
    max-height: 280px;
    height: auto;
    width: 400px;
    margin-top: 20px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 15px;
    opacity: 0.8;
}

#imgRightSide img{
    max-height: 260px;
    height: auto;
    max-width:380px ;
    min-width: 280px;
    width: auto;
}




